import {
    getValue as _getValue,
    getLastTh as _getLastTh,
} from '@webui-one/core/jsonrpc';
import { isNonEmptyString } from '@webui-one/core/utils/string';
import { hasProp } from '@webui-one/core/utils/object';
import * as actionTypes from './actionTypes';

export const ACTION_STATUS_INDICATOR_TIMEOUT = 'action-status-indicator-timeout';

export const getGlobalApplicationSettingsStartAction = () => ({
    type: actionTypes.GET_GLOBAL_APPLICATION_SETTINGS_START,
});

export const getGlobalApplicationSettingsErrorAction = ({ error }) => ({
    type: actionTypes.GET_GLOBAL_APPLICATION_SETTINGS_ERROR,
    error,
});

export const getGlobalApplicationSettingsCompleteAction = ({ setting, value }) => ({
    type: actionTypes.GET_GLOBAL_APPLICATION_SETTINGS_COMPLETE,
    setting,
    value,
});

export const getGlobalApplicationSetting = ({
    setting,
    funcs: {
        getLastTh = _getLastTh,
        getValue = _getValue,
    } = {},
} = {}) => (
    async (dispatch) => {
        try {
            dispatch(getGlobalApplicationSettingsStartAction());

            if (!isNonEmptyString(setting)) {
                throw new Error(`Invalid input parameter '${setting}'. Must be string`);
            }

            const th = await getLastTh();
            const path = `/webui/applications/${setting}`;
            const response = await getValue({
                th,
                path,
            });

            if (!hasProp(response, 'value')) {
                return dispatch(getGlobalApplicationSettingsCompleteAction({
                    setting,
                    value: undefined,
                }));
            }

            return dispatch(getGlobalApplicationSettingsCompleteAction({
                setting,
                value: response.value,
            }));
        } catch (error) {
            return dispatch(getGlobalApplicationSettingsErrorAction({ error }));
        }
    }
);
