import React from 'react';
import PropTypes from 'prop-types';
import { labelTypes } from 'modules/devices/selectors';
import style from './SummaryStyle';

const getDeviceGroupLogicsString = (values) => {
    const include = values.filter(
        value => value.type === labelTypes.LBL_INCLUDE).map(item => `"${item.name}"`);
    const oneOf = values.filter(
        value => value.type === labelTypes.LBL_ONE_OF).map(item => item.name);
    const exclude = values.filter(
        value => value.type === labelTypes.LBL_EXCLUDE).map(item => item.name);

    let returnString = '';
    const includeString = include.join(' AND ');
    if(includeString) returnString += ` ${includeString}`;
    const oneOfString = oneOf.join(', ');
    if(oneOfString) {
        returnString += returnString ? ' AND' : '';
        returnString += (oneOf.length > 1) ? ' ONE OF' : '';
        returnString += ` "${oneOfString}"`;
    }
    const excludeString = exclude.join(', ');
    if (excludeString) {
        returnString += returnString ? ' AND' : '';
        returnString += (exclude.length > 1) ? ' NOT ANY OF' : ' NOT';
        returnString += ` "${excludeString}"`;
    }
    return returnString;
};

const getString = (unit, keyword, values) => {
    const theLogics = getDeviceGroupLogicsString(values);
    let summary = unit;
    if (keyword) {
        summary += ` including "${keyword}"`;
        if (theLogics) summary += ', AND';
    } else if (theLogics) summary += ' where';
    if (theLogics) summary += ` group is${theLogics}`;
    return summary;
};

const Summary = ({ unit, labels, keyword }) => (<div style={style} data-handle="summary">
    {getString(unit, keyword, Object.values(labels))}
</div>);

Summary.propTypes = {
    labels: PropTypes.shape(Object),
    keyword: PropTypes.string,
    unit: PropTypes.string,
};

Summary.defaultProps = {
    labels: {
    },
    keyword: '',
    unit: '',
};

export default Summary;

export {
    getString as _getString,
};
