import React from 'react';
import DiffTable from '@webui-one/components/Diff/DiffTable';
import { timeStampToISOLocale } from '@webui-one/core/utils/time';
import actionResultStyle from './ActionResultStyle';
import { getInfoResult } from './selectors';

const DIFF_ACTIONS = Object.freeze([
    'compare-config',
    're-deploy dry-run',
]);
const DIFF_KEYS = Object.freeze([
    'diff',
    'cli/local-node/data',
]);

const renderError = (error, style) =>
    error && <div data-handle="error" style={style.output}>
        <div data-handle="message">{error.message}</div>
        {error.data && Object.entries(error.data).map(([key, value]) =>
            <div key={key}><strong>{key}: </strong>{value}</div>,
        )}
    </div>;


const renderGeneralResult = (result, style) => {
    if (Array.isArray(result)) {
        return result.map(r => (
            <div data-handle={`result ${r.name}`} key={r.name} style={style.output}>
                <pre>{r.value}</pre>
            </div>
        ));
    }
    if (result === undefined) return false;
    return <div style={style.result}>{result}</div>;
};

const renderDiffResult = (result) => {
    if(!result) return <div/>;
    const diff = result.find(r => DIFF_KEYS.includes(r.name));
    if (diff === undefined) {
        const info = getInfoResult(result);
        return <div>{info}</div>;
    } else {
        return <DiffTable data={diff.value} />;
    }
};

// eslint-disable-next-line import/prefer-default-export
export const createResultCellFactory = (style = actionResultStyle) =>
    // eslint-disable-next-line react/prop-types
    ({ isExpanded, yangAction }, column, rowKey) => {
        if (!isExpanded || !yangAction || (yangAction.result === undefined && !yangAction.error)) {
            return false;
        }
        const hasDiffResult = DIFF_ACTIONS.includes(yangAction.action);
        const status = yangAction.actionStatus.status;
        return (
            <div data-handle={`expanded-${yangAction.action}-${rowKey}`} style={style.wrapper}>
                <div style={{ display: 'flex' }}>
                    <div style={style.header}>
                        <div>Action:</div>
                        <div>{yangAction.action}</div>
                    </div>
                    <div style={{ ...style.header, margin: '0 2rem' }}>
                        <div>Performed:</div>
                        <div>{timeStampToISOLocale(yangAction.completedAt)}</div>
                    </div>
                    <div style={style.header}>
                        <div>Status:</div>
                        <div>{yangAction.status}</div>
                    </div>
                </div>
                <div style={style.result}>
                    <span>Result: </span>
                    <span style={{ color: status.color }}>{status.message}</span>
                </div>
                <div>
                    {hasDiffResult && renderDiffResult(yangAction.result)}
                    {!hasDiffResult && renderGeneralResult(yangAction.result, style)}
                    {renderError(yangAction.error, style)}
                </div>
            </div>);
    };

export {
    renderDiffResult as _renderDiffResult,
};
